.items-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }

.add-button {
  bottom: 2.5em;
  right: 2.5em;
  position: fixed;
  text-align: center;
  height: 2em;
  width: 2em;
  vertical-align: middle; }

.missions-container {
  display: flex;
  flex-wrap: nowrap;
  margin: 0;
  padding: 2em 0 1em .9em;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar; }

.missions-container::-webkit-scrollbar {
  display: none; }

.mission {
  background: #2ECC71;
  border-radius: .5em;
  color: white;
  font-size: 1.2em;
  padding: .3em 1em;
  margin: 0 .8em 0 0;
  min-width: 5em;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  display: flex;
  flex: 0 0 auto; }

.missions-container::after {
  content: "";
  flex: 0 0 1em; }

.green {
  background-color: #2ECC71; }

.yellow {
  background-color: #F1C40F; }

.purple {
  background-color: #8E44AD; }

.light-grey {
  background-color: #ECF0F1;
  color: #7F8C8D; }

.tasks {
  color: #7F8C8D;
  padding: 1.2em 0; }
  .tasks .subtitle {
    color: inherit;
    font-weight: bold;
    padding: 1em 1em 0; }

.rc-swipeout {
  margin: 1em;
  border-radius: .5em;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16); }

.task {
  display: flex;
  align-items: center;
  padding-left: 1em;
  /*border-bottom: 1px solid #DDDDDD;*/
  /* Styling Checkbox Starts */
  /* For Ripple Effect */ }
  .task input:focus {
    outline: none; }
  .task .completed {
    text-decoration: line-through; }
  .task .checkbox-container {
    float: left;
    box-sizing: border-box;
    text-align: center;
    padding-right: 10px; }
  .task .checkbox-label {
    display: block;
    position: relative;
    margin: auto;
    cursor: pointer;
    font-size: 22px;
    line-height: 24px;
    height: 24px;
    width: 24px;
    clear: both; }
  .task .checkbox-label input {
    position: absolute;
    opacity: 0;
    cursor: pointer; }
  .task .checkbox-label .checkbox-custom {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 24px;
    width: 24px;
    background-color: transparent;
    border-radius: 5px;
    transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    border: 2px solid; }
  .task .checkbox-label input:checked ~ .checkbox-custom {
    /*background-color: red;*/
    border-radius: 5px;
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
    opacity: 1;
    /*border: 2px solid red;*/ }
  .task .checkbox-label .checkbox-custom::after {
    position: absolute;
    content: "";
    left: 12px;
    top: 12px;
    height: 0px;
    width: 0px;
    border-radius: 5px;
    border: solid #009BFF;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(0deg) scale(0);
    transform: rotate(0deg) scale(0);
    opacity: 1;
    transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out; }
  .task .checkbox-label input:checked ~ .checkbox-custom::after {
    -webkit-transform: rotate(45deg) scale(1);
    transform: rotate(45deg) scale(1);
    opacity: 1;
    left: 8px;
    top: 3px;
    width: 6px;
    height: 12px;
    border: solid #7F8C8D;
    border-width: 0 2px 2px 0;
    background-color: transparent;
    border-radius: 0; }
  .task .checkbox-label .checkbox-custom::before {
    position: absolute;
    content: "";
    left: 10px;
    top: 10px;
    width: 0px;
    height: 0px;
    border-radius: 5px;
    border: 2px solid #FFFFFF;
    -webkit-transform: scale(0);
    transform: scale(0); }
  .task .checkbox-label input:checked ~ .checkbox-custom::before {
    left: -3px;
    top: -3px;
    width: 24px;
    height: 24px;
    border-radius: 5px;
    -webkit-transform: scale(3);
    transform: scale(3);
    opacity: 0;
    z-index: 999;
    transition: all 0.3s ease-out;
    -webkit-transition: all 0.3s ease-out;
    -moz-transition: all 0.3s ease-out;
    -ms-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out; }
  .task .marker {
    min-width: 1em;
    height: .2em;
    border-radius: .5em; }
  .task .description {
    padding: 1em 0;
    padding-left: .5em;
    width: 100%;
    font-family: "Helvetica Neue";
    font-size: 12pt;
    color: #7F8C8D; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

